<template>
    <div id="pie" style="height: 700px; width: 100%"></div>
</template>

<script>
import 'echarts' 

export default {
  name: 'graph4',
  data() {
      return {  
        data: [
        {"list":[{"value":38253,"name":"火力发电"},{"value":6940,"name":"水力发电"},{"value":715,"name":"风力发电"},{"value":864,"name":"核能发电"},{"value":229,"name":"太阳能发电"}]},
        {"list":[{"value":38555,"name":"火力发电"},{"value":8609,"name":"水力发电"},{"value":1008,"name":"风力发电"},{"value":974,"name":"核能发电"},{"value":232,"name":"太阳能发电"}]},
        {"list":[{"value":42359,"name":"火力发电"},{"value":9116,"name":"水力发电"},{"value":1149,"name":"风力发电"},{"value":1080,"name":"核能发电"},{"value":272,"name":"太阳能发电"}]},
        {"list":[{"value":42337,"name":"火力发电"},{"value":10643,"name":"水力发电"},{"value":1534,"name":"风力发电"},{"value":1325,"name":"核能发电"},{"value":657,"name":"太阳能发电"}]},
        {"list":[{"value":42420,"name":"火力发电"},{"value":11264,"name":"水力发电"},{"value":1863,"name":"风力发电"},{"value":1708,"name":"核能发电"},{"value":851,"name":"太阳能发电"}]},
        {"list":[{"value":44371,"name":"火力发电"},{"value":11934,"name":"水力发电"},{"value":2113,"name":"风力发电"},{"value":2133,"name":"核能发电"},{"value":874,"name":"太阳能发电"}]},
        {"list":[{"value":46627,"name":"火力发电"},{"value":11898,"name":"水力发电"},{"value":3057,"name":"风力发电"},{"value":2481,"name":"核能发电"},{"value":888,"name":"太阳能发电"}]},
        {"list":[{"value":50739,"name":"火力发电"},{"value":12342,"name":"水力发电"},{"value":3660,"name":"风力发电"},{"value":2944,"name":"核能发电"},{"value":1433,"name":"太阳能发电"}]},
        {"list":[{"value":52202,"name":"火力发电"},{"value":13044,"name":"水力发电"},{"value":4057,"name":"风力发电"},{"value":3484,"name":"核能发电"},{"value":2247,"name":"太阳能发电"}]},
        {"list":[{"value":53302,"name":"火力发电"},{"value":13552,"name":"水力发电"},{"value":4665,"name":"风力发电"},{"value":3663,"name":"核能发电"},{"value":2609,"name":"太阳能发电"}]},
        {"list":[{"value":58059,"name":"火力发电"},{"value":13390,"name":"水力发电"},{"value":6256,"name":"风力发电"},{"value":4075,"name":"核能发电"},{"value":3652,"name":"太阳能发电"}]},
        ],
        list:[]
      }
  },
  methods: {
    getdata() {
        this.list = this.data[0].list
      },
    drawPie (myPie){
        //图表的配置项和数据
        var that = this;
        var option = {
          title: {
            text: '我国发电方式概况(2011-2021)',
            subtext:"数据来源：国家及地方统计局\n单位：亿千瓦时",
            left:'11%',
            y:'10%',
            
            textStyle: {
                        fontSize:30,
                        color:'#ffd900',
                    },
            textAlign:'left'
          },
          tooltip: {
            trigger: 'item',
            formatter:'产业类型:{b}<br/> 发电量:{c}',
            textStyle:{
                    color:"#0054a4",
                  },
          },
           feature: {
              mark: { show: true },
          },        
          // 引入时间轴
          timeline:{
            x:100,
            realtime:false,
            width: 670,
            axisType:'category',
            label:{
              color:'#56a0d3',
            },
            playInterval:2000,
            left:'12%',
          bottom:'12%',
          autoPlay: true,
          loop: true,
          //currentIndex:0

          data: ['2011','2012','2013','2014','2015','2016','2017','2018','2019','2020','2021'],
          tooltip:{
            // 显示触发类型, item 在 hover 时显示
              trigger: 'item',
              // 地图 : {b}（区域名称），{c}（合并数值）, {d}（无）
              formatter: '年度：{b}<br/> '
          }
        },
          series: [
            {
              name: '产业类型及用电量',
              type: 'pie',
              radius: [50, 150],
              center: ['33%', '50%'],
              roseType: 'radius',
              itemStyle: {
                borderRadius: 10
              },
              color: [
                '#405de6',
                '#5851db',
                '#833ab4',
              ],
              labelLine: {
                lineStyle: {
                  borderRadius: 8
                },
                smooth: 0.2,
                length: 10,
                length2: 20
              },
              label: {
                fontWeight: 'bold',
                fontSize: 15,
                color:"#007fbd"
              },
              data: that.list
            }
          ]
};
        //使用刚指定的配置项和数据显示图表。
        setTimeout(()=>{
        option && myPie.setOption(option,true);
      },100) 

        myPie.on('timelinechanged',function(timeLineIndx){
            var res = timeLineIndx.currentIndex
            //console.log(res);
            that.list = []
            for(var i = 0; i < 1 ;i++){
                that.list = (that.data[res].list)
            }
            //console.log(this.list)
            setTimeout(()=>{
                myPie.setOption({
                  series: [{
                  data: that.list
                  },
                  ]
                })
            },100) 
       })
    },
  },
    created(){
        this.getdata();
        
    },

    mounted(){
        var myPie = this.$echarts.init(document.getElementById('pie'));
        this.drawPie(myPie);
  },
};


</script>

