<template>
    <div id="graph15" style="width:700px;height:500px;left:150px;top:300px"></div>
</template>

<script>
import 'echarts' 

export default {
  name:"Graph15",
  data(){
      return{}
  },
  methods:{
      draw(graph15){
    var option = {
    tooltip: {
        trigger: 'axis',
    axisPointer: {
      type: 'cross',
      crossStyle: {
        color: '#999'
      }
    }
  },
  title: {
            text: '我国年度用电量增长情况(2011-2021)',
            x:'2%',
            y:'0%',
            subtext:'数据来源：国家能源局',
            textStyle: {
                        fontSize:20,
                        color:'#ffd900',
                    },
            textAlign:'left'
          },
  toolbox: {
    x:'35%',
    y:'7%',
    feature: {
      dataView: { show: true, readOnly: false },
      magicType: { show: true, type: ['line', 'bar'] },
      restore: { show: true },
      saveAsImage: { show: true }
    }
  },
  legend: {
    data: ['全社会发电量增长量', '全社会发电量增速'],
    x:'55%',
    y:'8%',
    textStyle:{
      color:'#ffd900',
    },
  },
  grid:{
    y:'20%'
  },
  xAxis: [
    {
      type: 'category',
      data: ['2011','2012','2013','2014','2015','2016','2017','2018','2019','2020','2021'],
      axisPointer: {
        type: 'shadow'
      }
    }
  ],
  yAxis: [
    {
      type: 'value',
      name: '电量（亿千瓦时）',
      min: 0,
      max: 10000,
      interval: 1000,
      axisLabel: {
        formatter: '{value}'
      }
    },
    {
      type: 'value',
      name: '增速（%）',
      min: 0,
      max: 15,
      interval: 1.5,
      axisLabel: {
        formatter: '{value} %'
      }
    }
  ],
  series: [
    {
      name: '全社会发电量增长量',
      type: 'bar',
      color:"#553a99",
      tooltip: {
        valueFormatter: function (value) {
          return value + ' 亿千瓦时';
        }
      },
      data: [
        5005,2663,3622,2010,267,3698,3879,5372,3806, 2855, 8018
      ]
    },
    {
      name: '全社会发电量增速',
      type: 'line',
      yAxisIndex: 1,
      color:"#00d1b2",
      tooltip: {
        valueFormatter: function (value) {
          return value + ' %';
        }
      },
      data: [6.47,11.94,5.67,7.32,3.78,0.48,6.66,6.55,8.52,5.56, 3.95, 10.68]
    },
  ]
};
    setTimeout(() =>{
        option && graph15.setOption(option);
    },100)
      }
  },
  mounted(){
        var graph10 = this.$echarts.init(document.getElementById('graph15'));
        this.draw(graph10);
  },
}
</script>

