<template>
    <div id="graph6" style="width:100%;height:100%"></div>
</template>

<script>
import 'echarts' 

export default {
  name:"Graph6",
  data(){
      return{}
  },
    mounted() {
        this.init()
    },
    methods:{
        init(){
            var option = {
                textStyle:{
                    color:'#ffd900',
                },
            angleAxis: {
                
            },
            radiusAxis: {
                z :10,
                type: 'category',
                data: ['中国', '亚太地区','欧洲、北美','全球'],
                axisLabel:{
                    show:true,
                    interval : 0,
                    textStyle:{
                        color:"#FFF"
                    }
                },
            },
            
            polar: {
                center:['58%','55%']
            },
            title: {
            text: '全球主要发电能源结构',
            subtext:"圆心角代表占比\n数据来源：英国石油公司.世界能源统计年鉴[M].英国.2019.",
            x:'0%',
            y:'2%',
            textStyle: {
                        fontSize:20,
                        color:'#ffd900',
                    },
            textAlign:'left'
          },
            tooltip: {
                    show: true,
                    trigger: 'item',
                    formatter: "地区:{b}<br/> 能源占比: {c}%"
                },

            series: [
                {
                type: 'bar',
                data: [0.14, 0.90, 1.10, 2.53],
                coordinateSystem: 'polar',
                name: '石油',
                stack: 'a',
                color:'#6a737b',
                emphasis: {
                    focus: 'series'
                },
                },
                {
                type: 'bar',
                data: [3.19, 10.67, 29.44, 22.90],
                coordinateSystem: 'polar',
                name: '天然气',
                color:'#8db9ca',
                stack: 'a',
                emphasis: {
                    focus: 'series'
                }
                },
                {
                type: 'bar',
                data: [65.56, 56.92, 17.66, 35.99],
                coordinateSystem: 'polar',
                name: '煤炭',
                stack: 'a',
                color:'#8a7967',
                emphasis: {
                    focus: 'series'
                }
                },
                {
                type: 'bar',
                data: [4.77, 5.10, 19.18, 9.84],
                coordinateSystem: 'polar',
                name: '核能',
                color:'#48a9c5',
                stack: 'a',
                emphasis: {
                    focus: 'series'
                }
                },
                {
                type: 'bar',
                data: [15.23, 13.23, 14.05, 15.01],
                coordinateSystem: 'polar',
                name: '水',
                stack: 'a',
                color:'#037ef3',
                emphasis: {
                    focus: 'series'
                }
                },
                {
                type: 'bar',
                data: [13.50, 12.08, 17.64, 12.85],
                coordinateSystem: 'polar',
                name: '可再生资源',
                stack: 'a',
                color:'#00bce4',
                emphasis: {
                    focus: 'series'
                }
                },
                {
                type: 'bar',
                data: [0.59, 1.8, 0.93, 0.89],
                coordinateSystem: 'polar',
                name: '其他',
                stack: 'a',
                emphasis: {
                    focus: 'series'
                }
                }
            ],
            legend: {
                show: true,
                data: ['石油', '天然气', '煤炭','核能','水','可再生资源','其他'],
                orient: 'vertical',
                itemHeight: 15,//图例图标的高度
                textStyle: {
                    fontSize:15,
                    color:'#ffd900',
                    },
                x:'2%',
                y:'40%'
            }
            };
        var chartDom = document.getElementById('graph6');
        var myChart = this.$echarts.init(chartDom);
        myChart.showLoading();
        setTimeout(()=>{
            myChart.hideLoading();
            myChart.setOption(option);
        },50) 
        }
    }
}

</script>

