    <div class="button">
          <el-menu background-color=transparent text-color="#ffd900" mode="horizontal" >
            <el-menu-item @click="goto1" id="goto1"><template slot="title"><i class="el-icon-location"></i><span>首页</span></template></el-menu-item>
            <el-menu-item @click="goto1_2" id="goto1_2"><template slot="title"><i class="el-icon-location"></i><span>我们发了多少电</span></template></el-menu-item>
            <el-menu-item @click="goto1_3" id="goto1_3"><template slot="title"><i class="el-icon-location"></i><span>我们用了多少电</span></template></el-menu-item>
            <el-menu-item @click="goto1_4" id="goto1_4"><template slot="title"><i class="el-icon-location"></i><span>我们的电够用吗</span></template></el-menu-item>
             <el-menu-item @click="goto2_1" id="goto2_1"><template slot="title"><i class="el-icon-location"></i><span>为什么会限电</span></template></el-menu-item>
             <el-menu-item @click="goto2_2" id="goto2_2"><template slot="title"><i class="el-icon-location"></i><span>结语</span></template></el-menu-item>
