<script>

import Home from './home/home.vue'
import Home1 from './part1/1.1/home1.vue'
import Page11 from './part1/1.1/1_1.vue'
import Page12 from './part1/1.2/1_2.vue'
import Page13 from './part1/1.3/1_3.vue'
import Page21 from './part2/2.1/2_1.vue'
import End from './part2/2.2/end.vue'

export default {
  name: "Main",
  components:{  
    Home,
    Home1,
    Page11,
    Page12,
    Page13,
    Page21,
    End
  },
  methods:{
    goto1(){
      this.$el.querySelector('.home').scrollIntoView({
        behavior:"smooth",
        block:"start"
      })
    },
    goto1_2(){
      this.$el.querySelector('.home1').scrollIntoView({
        behavior:"smooth",
        block:"start"
      })
    },
    goto1_3(){
      this.$el.querySelector('.page12').scrollIntoView({
        behavior:"smooth",
        block:"start"
      })
    },
    goto1_4(){
      this.$el.querySelector('.page13').scrollIntoView({
        behavior:"smooth",
        block:"start"
      })
    },
    goto2_1(){
      this.$el.querySelector('.page21').scrollIntoView({
        behavior:"smooth",
        block:"start"
      })
    },
    goto2_2(){
      this.$el.querySelector('.end').scrollIntoView({
        behavior:"smooth",
        block:"start"
      })
    },
      goAnchor(selector) { 
            var anchor = this.$el.querySelector(selector) // 获取元素
            if (anchor) {
                setTimeout(() => { // 页面没有渲染完成时是无法滚动的，因此设置延迟
                    anchor.scrollIntoView({behavior: 'smooth'}) // js的内置方法，可滚动视图位置至元素位置
                }, 500)
            }
        },
      jump(val) { // 点击跳转的方法 val 你定义的需要跳转的标签id 此方法可以根据自己的实际情况编辑
          this.goAnchor('#' + val)
        },
      //点击跳转方法
      toPath(path) {
      this.$router.push({path: path})
        }
  },
  mounted(){
  }
}
