<template>
    <div id="graph16" style="width:50%;height:600px;left:800px;top:-300px"></div>
</template>

<script>
import 'echarts' 

export default {
  name:"Graph16",
  data(){
      return{}
  },
  methods:{
    draw(){
        var graph16 = this.$echarts.init(document.getElementById('graph16'));
    var option = {
        legend: {
      y:"50%",
      x:"42%",
      textStyle:{
        color:'#ffd900',
    },
    },
    title:{
            text: '我国年度具体用电量增长情况',
            subtext:'数据来源：国家能源局\n\n上图为增量占比，下图为增速对比',
            x:'6%',
            y:'40%',
            textStyle: {
                        fontSize:20,
                        color:'#ffd900',
                    },
            },
    tooltip: {
      trigger: 'axis',
      showContent: false
    },
    dataset: [{
      source: [
        ['product', '2019', '2020', '2021'],
        ['第一产业',4.5, 10.2, 16.4],
        ['第二产业',3.1, 2.5, 9.1],
        ['第三产业', 9.5, 1.9, 17.8],
        ['居民用电', 5.7, 6.9, 7.3]
        ],
      },{
      source:[
        ['product', '2019', '2020', '2021'],
        ['第一产业',52, 79, 164],
        ['第二产业',2127, 1853, 4916],
        ['第三产业', 1062, 224, 2144],
        ['居民用电', 565, 699, 794]
      ],
      }
    ],
    xAxis: { type: 'category' },
    yAxis: { gridIndex: 0 },
    grid: { top: '55%' },
    series: [
      {
        datasetIndex:0,
        type: 'line',
        smooth: true,
        seriesLayoutBy: 'row',
        emphasis: { focus: 'series' },
        label: {
          formatter: '{b}: {@2019}亿千瓦时 ({d}%)',
          textStyle:{
            color:'#ffd900',
            },
        },
      },
      {
        type: 'line',
        datasetIndex:0,
        smooth: true,
        seriesLayoutBy: 'row',
        emphasis: { focus: 'series' }
      },
      {
        type: 'line',
        datasetIndex:0,
        smooth: true,
        seriesLayoutBy: 'row',
        emphasis: { focus: 'series' }
      },
      {
        type: 'line',
        datasetIndex:0,
        smooth: true,
        seriesLayoutBy: 'row',
        emphasis: { focus: 'series' }
      },
      {
        type: 'pie',
        id: 'pie',
        datasetIndex:1,
        radius: '30%',
        center: ['50%', '30%'],
        emphasis: {
          focus: 'self'
        },
        label: {
          formatter: '{b}: {@2019}亿千瓦时 ({d}%)',
          textStyle:{
            color:'#ffd900',
            },
        },
        encode: {
          itemName: 'product',
          value: '2019',
          tooltip: '2019'
        }
      }
    ]
  };
  graph16.on('updateAxisPointer', function (event) {
    const xAxisInfo = event.axesInfo[0];
    if (xAxisInfo) {
      const dimension = xAxisInfo.value + 1;
      graph16.setOption({
        series: {
          id: 'pie',
          label: {
            formatter: '{b}: {@[' + dimension + ']} ({d}%)'
          },
          encode: {
            value: dimension,
            tooltip: dimension
          }
        }
      });
    }
  });
   setTimeout(() =>{
        option && graph16.setOption(option);
    },100)
}
  },
  mounted(){
        this.draw();
  },
}
</script>

