<template>
  <div>
    <div id="graph7" style="width:100%;height:700px;"></div>
  </div>
</template>

<script>
/* eslint-disable */
//import china from './china';
import 'echarts';
import china from './data/china.json';

export default {
  name: "Graph7",
  data() {
    return {
    }
  },
  mounted() {
    this.init()
  },
  methods: {      
      init(){
          // 柱状图 图中的位置
      var center = {
