        青海省:[101.778916,36.623178],
       山东省:[117.000923,36.675807],
        山西省:[112.549248,37.857014],
        陕西省:[108.948024,34.263161],
        上海市:[121.472644,31.231706],
        天津市:[117.190182,39.125596],
        西藏自治区:[91.132212,29.660361],
        新疆维吾尔自治区:[87.617733,43.792818],
        云南省:[102.712251,25.040609],
        浙江省:[120.153576,30.287459],
      }
      var data1 = [
                            { name: '北京市', value: 40269},
                            { name: '重庆市', value: 27894},
                            { name: '四川省', value: 53850},
                            { name: '安徽省', value: 42959},
                            { name: '福建省', value:  48810},
                            { name: '甘肃省', value:  10243},
                            { name: '广东省', value:  123470},
                            { name: '广西壮族自治区', value: 24741 },
                            { name: '贵州省', value: 19586 },
                            { name: '海南省', value: 6475},
                            { name: '河北省', value: 40391 },
                            { name: '河南省', value: 58887 },
                            { name: '黑龙江省', value: 14879 },
                            { name: '湖北省', value: 50012 },
                            { name: '湖南省', value: 46063 },
                            { name: '吉林省', value: 13236 },
                            { name: '江苏省', value: 116364 },
                            { name: '江西省', value: 29620 },
                            { name: '辽宁省', value: 27584 },
                            { name: '内蒙古自治区', value: 20514 },
                            { name: '宁夏回族自治区', value: 4522 },
                            { name: '青海省', value: 3347 },
                            { name: '山东省', value: 83096 },
                            { name: '山西省', value: 22590 },
                            { name: '陕西省', value: 29801},
                            { name: '上海市', value: 43214 },
                            { name: '天津市', value: 15695 },
                            { name: '西藏自治区', value: 2080 },
                            { name: '新疆维吾尔自治区', value: 15984 },
                            { name: '云南省', value: 27147 },
                            { name: '浙江省', value: 73516 },
                        ]
      var data2 = [
                            { name: '北京市', value: 1232},
                            { name: '重庆市', value: 1341},
                            { name: '四川省', value: 3275},
                            { name: '安徽省', value: 2715},
                            { name: '福建省', value:  2837},
                            { name: '甘肃省', value:  1495},
                            { name: '广东省', value:  7867},
                            { name: '广西壮族自治区', value: 2236 },
                            { name: '贵州省', value: 1743 },
                            { name: '海南省', value: 405},
                            { name: '河北省', value: 4294 },
                            { name: '河南省', value: 3647 },
                            { name: '黑龙江省', value: 1089 },
                            { name: '湖北省', value: 2472 },
                            { name: '湖南省', value: 2155 },
                            { name: '吉林省', value: 843 },
                            { name: '江苏省', value: 7101 },
                            { name: '江西省', value: 1863 },
                            { name: '辽宁省', value: 2576 },
                            { name: '内蒙古自治区', value: 3957 },
                            { name: '宁夏回族自治区', value: 1158 },
                            { name: '青海省', value: 858 },
                            { name: '山东省', value: 7383 },
                            { name: '山西省', value: 2608 },
                            { name: '陕西省', value: 1966},
                            { name: '上海市', value: 1750 },
                            { name: '天津市', value: 982 },
                            { name: '西藏自治区', value: 101 },
                            { name: '新疆维吾尔自治区', value: 3527 },
                            { name: '云南省', value: 2138 },
                            { name: '浙江省', value: 5514 },
                        ]
        var convertData = function(data){
            var res = [];
            for (var i = 0; i < data.length; i++){
                var geoCoord = center[data[i].name];
                if(geoCoord){
                    res.push({
                        name:data[i].name,
                        value:geoCoord.concat(data[i].value),
                    });
                }
            }
            return res;
        };
            var option = {
                title: {
                    text: '我国各省市年度用电量与GDP概况',
                    subtext:"数据来源：国家能源局\n颜色及散点大小代表高低",
                    left: '20%',
                    top:'5%',
                    textStyle: {
                        fontSize:25,
                        color:'#ffd900',
                    },
                    
                },
                brush:{
                    toolbox:['rect','polygon','linex','lineY','keep']
                },
                tooltip: {
                    triggerOn: 'mousemove',
                    formatter:function(params){
                        if(isNaN(params.value)){
                            params.value = 0
                        }
                        if(params.seriesName == '散点'){
                            var value;
                            value = params.data.value[2]
                            var html = '<span>地区:' + params.name + '</span><br/>'
                            html += '<span>用电量：' + value + '(亿千瓦时)</span><br/>'
                            return html
                        }
                        if(params.seriesName == '地区GDP'){
                            var value = params.data.value
                            var html = '<span>地区:' + params.name + '</span><br/>'
                            html += '<span>GDP：' + value + '(亿元)</span><br/>'
                            return html
                        }
                    },
                    showDelay: 0,
                    transitionDuration: 0.2
                    },
                visualMap: {
                    seriesIndex:0,
                        left: '71%',
                        min: 2000,
                        max: 123470,
                        inRange: {
                            symbolSize: [30, 100],
                            color: [
                                '#34bf49',
                                '#5ecc62',
                                '#89ba16',
                                '#e4e932',
                                '#fff200',
                                '#ffffbf',
                                '#ffffbf',
                                '#ffc845',
                                '#ffc845',
                                '#ffc845',
                                '#de0f17',
                                '#de0f17',                             
                                '#de0f17',
                                '#de0f17',
                                '#de0f17',
                                '#de0f17'
                                ]
                            },
                        text: ['高', '低'],
                            calculable: true,
                            top:'38%',
                            textStyle:{
                                color:'#ffd900',
                            }
                        },
                        toolbox: {
                            show: true,
                            orient: 'vertical',
                            left: '75%',
                            top: '10%',
                            feature: {
                                dataView: { readOnly: false },
                                restore: {},
                                saveAsImage: {}
                            }
                        },
                       geo:[{
                           map:'china',
                           type:'map',
                           roam:true,
                           left:'30%',
                           top:'15%',
                           zoom:1.2,
                           animationDurationUpdate:0,
                       }],
                        series: [
                    {
                        name: '地区GDP',
                        type: 'map',
                        roam: true,
                        map: 'china1',
                        left:'30%',
                        top:'15%',
                        zoom:1.2,
                        emphasis: {
                        label: {
                            show: true
                        }
                        },
                        data:data1
                },
                {     
                        name:'散点',
                        type:'effectScatter',
                        coordinateSystem: 'geo', // series坐标系类型
                        data:convertData(data2),  //散点的坐标，自己定义一个var即可
                        showEffectOn: 'render',
                        rippleEffect:{   // 涟漪特效相关配置。
                            brushType:'stroke',   
                        },
                        symbolSize: function (params) {
                            if(params[2] < 200){
                                return params[2] / 20
                            }
                            else if (params[2] < 500){
                                return params[2] / 50
                            }
                            else return params[2] / 300
                        },
                        encode: {
                            value: 2
                        },
                        label:{                       
                            formatter:'{b}',
                            position:'top',
                            show:true,
                        },
                        itemStyle:{
                            shadowBlur: 10,
                            color:function(params){
                                var val = params.value[2]
                                if (val < 500){
                                    return '#0085ad'
                                }
                                else if (val < 2000){
                                    return '#ff9933'
                                }
                                else if (val < 5000){
                                    return '#e4002b'
                                }
                                else{
                                    return '#a51890'
                                }
                            }
                        },
                        emphasis:{
                            scale:true,
                            itemStyle:{
                                color:'red'
                            }
                        }
                }
                        
        ]
      }
        var chartDom = document.getElementById('graph7');
        var myChart = this.$echarts.init(chartDom);
        this.$echarts.registerMap('china1', china)
        this.$echarts.registerMap('china', china)
        myChart.showLoading();
        setTimeout(()=>{
            myChart.hideLoading();
            myChart.setOption(option);
        },50) 
        
        //捕捉georoam事件，使下层的geo随着上层的geo一起缩放拖曳
        myChart.on('georoam',function(params){
            var option = myChart.getOption();//获得option对象
            if(params.zoom!=null&&params.zoom!=undefined){ //捕捉到缩放时
                option.geo[0].zoom=option.series[0].zoom;//下层geo的缩放等级跟着上层的geo一起改变
                option.geo[0].center=option.series[0].center;//下层的geo的中心位置随着上层geo一起改变
            }else{//捕捉到拖曳时
                option.geo[0].center=option.series[0].center;//下层的geo的中心位置随着上层geo一起改变
            }
            myChart.setOption(option);//设置option
        });
  }
}
}
</script>

