<template>
    <div id="graph10" style="width:80%;height:500px;top:10%;left:170px"></div>
</template>

<script>
import 'echarts' 

export default {
  name:"Graph10",
  data(){
      return{}
  },
  methods:{
      draw(graph10){
    var option = {
    tooltip: {
        trigger: 'axis',
    axisPointer: {
      type: 'cross',
      crossStyle: {
        color: '#999'
      }
    }
  },
  title: {
            text: '我国各产业年度用电分布情况',
            x:'2%',
            y:'1%',
            textStyle: {
                        fontSize:20,
                        color:'#ffd900',
                    },

            textAlign:'left',
            subtext:"数据来源：中国电力企业联合会、国家能源局、国家统计局、各省统计局、全国能源信息平台",
          },
  toolbox: {
    x:'78%',
    y:'9%',
    feature: {
      dataView: { show: true, readOnly: false },
      magicType: { show: true, type: ['line', 'bar'] },
      restore: { show: true },
      saveAsImage: { show: true }
    }
  },
  legend: {
