        }
      },
      data: [
        49591, 53223, 55233, 55500, 59198, 63077, 68449, 72255, 75110, 83128
      ]
    },
    {
      name: '发电量增速',
      type: 'line',
      yAxisIndex: 1,
      color:'#a626aa',
      tooltip: {
        valueFormatter: function (value) {
          return value + ' %';
        }
      },
