        }
      },
      data: [5.67, 7.32, 3.78, 0.48,6.66, 6.55, 8.52, 5.56, 3.95, 10.68]
    }
  ]
};
    setTimeout(() =>{
        option && graph10.setOption(option);
    },100)
      }
  },
  mounted(){
        var graph10 = this.$echarts.init(document.getElementById('graph10'));
        this.draw(graph10);
  },
}
</script>

