<template>
    <div id="graph2" style="width:100%;height:100%"></div>
</template>

<script>
import 'echarts' 


export default {
  name:"Graph2",
  data(){
      return{}
  },
  mounted() {
        this.init()
    },
  methods:{
    init(){
    var option = {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'cross',
          crossStyle: {
            color: '#ffd900'
          }
        }
      },
      title: {
            text: '我国发电基本情况',
            x:'2%',
            y:'0%',
            textStyle: {
                        fontSize:20,
                        color:'#ffd900',
            },
            subtext:"数据来源：国家统计局\n\n交互方式：鼠标移动和按钮点击",
            subtextStyle:{
              color:"#f8dfc2"
            },
            textAlign:'left'
      },
      toolbox: {
        top:'12%',
        x:'65%',
        feature: {
          dataView: { show: true, readOnly: false },
          magicType: { show: true, type: ['line', 'bar'] },
          restore: { show: true },
          saveAsImage: { show: true }
        }
      },
      legend: {
        data: ['总发电量', '逐年发电量增速', '规模以上发电量'],
        textStyle:{
          color:'#ffd900',
        },
        top:'8%',
        x:'42%'
      },
      grid:{
        top:'22%'
      },
      xAxis: [
        {
          type: 'category',
          data: ['2011', '2012', '2013', '2014', '2015', '2016', '2017', '2018', '2019', '2020',
          '2021'],
          axisPointer: {
            type: 'shadow'
          }
        }
      ],
      yAxis: [
        {
          type: 'value',
          name: '发电量/亿千瓦时',
          min: 0,
          max: 100000,
          interval: 10000,
          axisLabel: {
            formatter: '{value} '
          }
        },
        {
          type: 'value',
          name: '百分比',
          min: 0,
          max: 15,
          interval: 1.5,
          axisLabel: {
            formatter: '{value} %',
          }
        }
      ],
      series: [
        {
          name: '总发电量',
          type: 'bar',
          tooltip: {
            valueFormatter: function (value) {
              return value + ' 亿千瓦时';
            }
          },
          data: [
            47001, 49378, 53976, 56496, 58106, 61425, 64951, 71118, 75034, 77791, 85343
          ]
        },
        {
          name: '规模以上发电量',
          type: 'bar',
          tooltip: {
            valueFormatter: function (value) {
              return value + ' 亿千瓦时';
            }
          },
          data: [
            46025, 48188, 52451, 54811, 56184, 59900, 63000, 68000, 71422, 74170, 81122
          ]
        },
        {
          name: '逐年发电量增速',
          type: 'line',
          yAxisIndex: 1,
          tooltip: {
            valueFormatter: function (value) {
              return value + ' %';
            }
          },
          lineStyle:{                 
                  width:2,  // 设置虚线宽度
                  type:'dotted'  // 虚线'dotted' 实线'solid'
                },
          data: [11.73, 5.06, 9.31, 4.67, 2.85, 5.71, 5.74, 9.49
          , 5.51, 3.67, 9.71]
        },
      ]
    };
    var chartDom = document.getElementById('graph2');
        var myChart = this.$echarts.init(chartDom);
        myChart.showLoading();
        setTimeout(()=>{
            myChart.hideLoading();
            myChart.setOption(option);
        },50) 
    }
  }
}

</script>

