                },
                smooth: 0.2,
                length: 10,
                length2: 20
              },
              label: {
                fontWeight: 'bold',
                fontSize: 15,
                color:"#007fbd"
              },
              data: that.list
            }
          ]
};
        //使用刚指定的配置项和数据显示图表。
        setTimeout(()=>{
        option && myPie.setOption(option,true);
      },100) 

        myPie.on('timelinechanged',function(timeLineIndx){
            var res = timeLineIndx.currentIndex
            //console.log(res);
            that.list = []
            for(var i = 0; i < 1 ;i++){
                that.list = (that.data[res].list)
            }
            //console.log(this.list)
            setTimeout(()=>{
                myPie.setOption({
